import { hotSearchAPI, searchResultAPI } from "@/api";
import SongItem from "@/components/SongItem.vue";
export default {
  data() {
    return {
      ss: "",
      hotList: [],
      resultList: [],
      timer: null
    };
  },

  async created() {
    //推荐歌单
    const res = await hotSearchAPI();
    this.hotList = res.data.result.hots;
    console.log(res);
  },

  methods: {
    async btn(str) {
      this.ss = str; // 把点击的关键字给输入框显示

      const res = await searchResultAPI({
        type: 1,
        keywords: this.ss
      });
      this.resultList = res.data.result.songs;
      console.log("sadasd", this.resultList);
      setTimeout(() => {
        clearTimeout(this.timer);
      }); //防止下面请求再次执行
    },

    playFn(zhi) {
      this.$router.push({
        path: "/play"
      });
    }

  },
  watch: {
    ss(val) {
      clearTimeout(this.timer); //判断输入框有没有值

      if (val.length === 0) return this.resultList = [];
      this.timer = setTimeout(async () => {
        const res = await searchResultAPI({
          type: 1,
          keywords: val
        });
        console.log(res);
        this.resultList = res.data.result.songs;
      }, 500);
    }

  },
  components: {
    SongItem
  }
};