import bus from "@/eventBus";
export default {
  props: {
    name: String,
    // 歌名
    author: String,
    // 歌手
    id: Number,
    // 歌曲id (标记这首歌曲-为将来跳转播放页做准备)
    yytps: String
  },

  data() {
    return {};
  },

  methods: {
    playFn(id, tp) {
      this.$router.push({
        path: '/play'
      });
      bus.$emit('czx', this.id), bus.$emit('czx2', this.yytps);
    }

  }
};