import { recommendMusicAPI, hotMusicAPI } from "@/api";
import SongItem from "@/components/SongItem.vue";
export default {
  data() {
    return {
      recommendList: [],
      hotList: []
    };
  },

  methods: {
    playFn() {
      this.$router.push({
        path: "/play"
      });
    }

  },

  async created() {
    //推荐歌单
    const res = await recommendMusicAPI({
      limit: 6
    });
    this.recommendList = res.data.result; //推荐新音乐

    const songRes = await hotMusicAPI({
      limit: 100
    });
    console.log(songRes);
    this.hotList = songRes.data.result;
  },

  components: {
    SongItem
  }
};